<template>
<div>
  <input type="text" @keydown.tab.prevent="callTab">
  <!-- 커서를 input상자에 먼저 두고 탭키를 눌러보기(개발자모드에서 확인해보기) -->
  <p>{{ sampleData }}</p>
 </div>
</template>

<script>
export default {
  components: [],
  data() {
    return {
      sampleData: ''
    }
  },
  beforeCreate() {
    console.log('beforeCreate() ..')
  },
  created() { //메모리에 올라갔을때(this는 메모리에 올라갔을때부터 사용됨.)
    console.log('created() ..')
  },
  beforeMount() {
    console.log('beforeMount() ..')
  },
  mounted() { //화면에 올라갔을때
    console.log('mounted() ..')
  },
  beforeUpdate() {
    console.log('beforeUpdate() ..')
  },
  updated() { //업데이트
    console.log('updated() ..')
  },
  beforeUnmount() {
    console.log('beforeUnmount() ..')
  },
  unmounted() { //소멸될때
    console.log('unmounted() ..')
  },
  methods: {
    callTab() {
      console.log('tab event')
    }
  }
}
</script>
<style scoped></style>